.body {
  padding: 10px;

  display: grid;
  grid-gap: 5px;
  grid-template-rows: 50px 200px 50px;
  grid-template-areas:
    "header"
    "content"
    "footer";

  justify-items: center;
}

.header {
  grid-area: header;
  background-color: grey;
}

.content {
  grid-area: content;
}

.footer {
  grid-area: footer;
}
